/*======================================
アイコン                                        
======================================*/
.icon-wrap {
  position: absolute;
  right: 19px;
  top: 50%;
  transform: translateY(-50%);
  width: 38px;
  height: 38px;
}
@media screen and (max-width: 768px) {
  .icon-wrap {
    width: 28px;
    height: 28px;
  }
}
@media screen and (max-width: 600px) {
  .icon-wrap {
    width: 20px;
    height: 20px;
  }
}

.icon {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
}

/* プラスアイコン */
/* ２本の線を.icon-wrapの中央に並行に重ねる */
.icon:before,
.icon:after {
  position: absolute;
  content: "";
  display: block;
  transition: all 0.4s;
  background: #707070;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 2px;
  transform: translate(-50%, -50%);
}

/* そのうち1本を縦にする */
.icon:before {
  transform: translate(-50%, -50%) rotate(90deg);
}

/* プラスアイコンクリック後、マイナスにする */
.icon.open:before {
  transform: translate(-50%, -50%) rotate(0deg);
}

