@use 'global' as *;

.mv-sub {

}
.mv-sub__inner {

}
.mv-sub__background {
    max-width: rem(646);
    width: 100%;
    margin: 0 auto;
    height: rem(110);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include mq('md') {
       height: rem(200); 
    }
}
.mv-sub__image {
    width: 100%;
    position: relative;
}
.mv-sub__image::before {
    content:"";
    display: block;
    padding-top: 22%; 
    @include mq('md') {
       padding-top: 30%; 
    }
}
.mv-sub__image img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.mv-sub__title-ja,
.mv-sub__title-en {
    color: $black;
    position: absolute;
}
.mv-sub__title-ja{
    font-size: min(2vw,36px);   
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: $notoserif;
    text-transform: uppercase;
    @include mq(md) {
        font-size: rem(28);
    }
    @include mq('sm') {
       font-size: rem(36); 
    }
}
.mv-sub__title-en {
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: $sub-font-family;
    text-transform:capitalize;
    @include mq(md) {
        top: 65%;
    }
}