@use 'global' as *;

.fade-in-up {
    position: absolute;
    opacity: 0;
    animation-name: fade-in-up;
    animation-delay: 1.5s;
    animation-duration: .8s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
  }

  @keyframes fade-in-up {
    0% {
       opacity: 0;
       transform: translateY(20px);
    }
    100% {
       opacity: 1;
       transform: translateY(0);
    }
  }