@use 'global' as *;

.reservation {

}

.reservation__inner {
    max-width: rem(938);
    width: 100%;
    margin: 0 auto;
    
    @include mq(md) {
        padding-left: $padding-sp;
        padding-right: $padding-sp;
    }
}

.reservation__container {
    background-color: $white;
    height: rem(176);
    width: rem(938);
    margin: 0 auto;
}

.reservation__title-area {
    padding-top: rem(36);
    color: $black;
    text-align: center;
}

.reservation__title--ja {
  font-size: rem(34);
}

.reservation__title--en {
    margin-top: rem(-6);
    margin-left: rem(28);
    font-family: $sub-font-family;
}

.reservation__icon {
    padding-top: rem(82);
    background-image :url(../images/common/arrow.png);
    background-position :center;
    background-repeat :no-repeat;
    width: rem(50);
    height: rem(30);
    margin: auto;
    @include mq('sm') {
       padding-top: 0; 
    }
}